import { Component, OnInit, ViewChild } from '@angular/core';
import { IAdministrador, IAdministradorResp, ICargo } from '../../interface/administrador';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AdministradorService } from '../../service/administrador.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { isCaracter, isNumber, isTel, telefonoRegex } from '../../../../constantes/constantesproyecto'
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { Usuario } from 'src/app/pages/usuario/models/usuario';

@Component({
  selector: 'app-lista-admin',
  templateUrl: './lista-admin.component.html',
  styleUrls: ['./lista-admin.component.scss']
})
export class ListaAdminComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  admin: IAdministrador[] = [];
  cargos: ICargo[] = [];
  usuarios: Usuario[];

  formAdmin!: FormGroup;
  formAdminCargo!: FormGroup;
  // adminE: IAdministrador;
  adminE: IAdministradorResp;
  cargosE: ICargo;
  error = ''
  showLoading = false;
  //------------------
  /**DataTables */
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any = {};
  dtTrigger: Subject<IAdministrador> = new Subject<IAdministrador>();

  constructor(private adminService: AdministradorService,
    private cargoService: AdministradorService,
    private modalService: NgbModal,
    private fb: FormBuilder,
    private fbdos: FormBuilder,
    private router: Router) {
    this.formAdmin = this.iniciarFormulario();
    this.formAdminCargo = this.iniciarFormularioCargoAdmin();
  }


  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Administrador' }, { label: 'Registros', active: true }];

    this.dtOptions = {
      columnDefs: [
        { orderable: false, targets: [4] },
        { searchable: false, targets: [0, 4] },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };

    this.cargarDatosAdmin();
    this.llenarComboCargos();
  }

  ngOnDestroy() { //para destruir la peticion
    this.dtTrigger.unsubscribe();
  }

  /**
    * Open modal
    * @param content modal content
    */
  openModal(content: any) {
    this.error = '';
    this.modalService.open(content, { backdrop: 'static', keyboard: false });
    this.formAdmin = this.iniciarFormulario(); //limpia los campos al editar
    this.adminE = null; //limpia el arreglo de los campos al editar
  }

  openModalCargo(content: any) {
    this.modalService.open(content, { backdrop: 'static', keyboard: false });
  }

  openModalA(content) {
    this.error = '';
    this.modalService.open(content, { keyboard: false, scrollable: true, size: 'lg' });
  }

  private iniciarFormulario(): FormGroup {
    return this.fb.group({
      id: ['',],
      nombres: ['', [Validators.required, Validators.minLength(4), Validators.pattern(isCaracter)]],
      apellidos: ['', [Validators.required, Validators.minLength(4), Validators.pattern(isCaracter)]],
      telefono: ['', [Validators.required, Validators.pattern(telefonoRegex)]],
      cargo: ['Seleccione', [Validators.required, Validators.pattern(isNumber)]],
    });
  }

  esCampoValido(campo: string) {
    const validarCampo = this.formAdmin.get(campo);
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }


  llenarComboCargos() {
    this.cargoService.mostrarCargos().subscribe((resp: any) => {
      this.cargos = resp;
    });
  }

  openModalEditar(content: any, admine: IAdministradorResp) {
    this.error = '';
    this.adminE = admine;
    this.formAdmin.patchValue(admine);
    // this.formAdmin.get('cargo').setValue(admine.cargo.id)
    this.formAdmin.get('cargo').setValue(admine.idCargo)
    this.modalService.open(content, { backdrop: 'static', keyboard: false });
  }

  cargarDatosAdmin(page: number = 0) {
    // this.adminService.mostrarAdmin().subscribe((resp: any) => {
    //   this.admin = resp;
    //   this.dtTrigger.next(null);
    // });
    this.adminService.mostrarAdminUser().subscribe((res: any) => {
      this.admin = res;
      this.dtTrigger.next(null);
    });
  }


  guardarAdmin() {
    if (this.formAdmin.valid) {
      if (this.adminE != null) {
        this.editandoAdminForm();
      } else {
        this.registrandoAdminForm();
      }

    } else {
      Swal.fire({
        confirmButtonColor: "#a90000",
        position: 'center',
        title: 'Faltan datos en el formulario',
        text: 'completar campos requeridos',
        icon: 'warning',
      });
    }

  }


  registrandoAdminForm() {
    this.showLoading = true
    const admin = this.formAdmin.value;
    if (!admin.telefono.match(telefonoRegex)) {
      return;
    } else if (this.verificar(admin.telefono)) {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: "Advertencia",
        text: `Teléfono ya registrado`,
        icon: "warning",
      });
    } else {
      this.adminService.nuevoAdmin(admin).subscribe((resp: any) => {

        if (resp) {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Almacenado correctamente`,
            icon: 'success',
          });
          this.formAdmin.reset();
          this.formAdmin.get('cargo').setValue('Seleccione');
          this.reload();
          this.modalService.dismissAll();
        }

      }, (err: any) => {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Advertencia',
          text: `Ocurrio un problema`,
          icon: 'warning',
        });
      });
    }


  }

  editandoAdminForm() {
    this.showLoading = true
    const admine = this.formAdmin.value;
    if (!admine.telefono.match(telefonoRegex)) {
      return;
    } else {
      this.adminService.editarAdmin(admine).subscribe((resp: any) => {

        if (resp) {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Modificado correctamente`,
            icon: 'success',
          });
          this.formAdmin.reset();
          this.formAdmin.get('cargo').setValue('Seleccione');
          this.reload();
          this.modalService.dismissAll();
        }
      }, (err: any) => {
        if(err.status === 422){
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: "Advertencia",
            text: `Teléfono ya registrado`,
            icon: "warning",
          });
        }

      });
    }
  }

  eliminandoconAlert(x: IAdministrador): void {
    Swal.fire({
      title: '¿Esta seguro de eliminar? ',
      text: `Esto no se puede revertir`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
      confirmButtonText: 'Si, borrar',
      cancelButtonText: 'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {
        this.adminService.deleteAdmin(x).subscribe((res: any) => {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Eliminado',
            text: `El registro ha sido eliminado`,
            icon: 'success',
          });
          this.reload();
        }, (err: any) => {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Advertencia',
            text: 'El registro no puede ser eliminado',
            icon: 'warning',
          });
        });

      } else if (result.isDenied) {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: "Información",
          text: `Cambios no aplicados`,
          icon: "info",
        });
      }
    });
  }

  verificar(tel: string): boolean {
    return this.admin.some(usu => usu.telefono === tel);
  }

  guardarCargosAdmin() {
    if (this.formAdminCargo.valid) {

      this.registrandoCargo();
    } else {
      Swal.fire({
        position: 'center',
        title: 'Faltan datos en el formulario',
        text: 'completar campos requeridos',
        icon: 'warning',
      });
    }

  }


  registrandoCargo() {
    this.showLoading = true
    const cargoAdmin = this.formAdminCargo.value;
    if (this.existeCargo(cargoAdmin.nombrecargo.toLowerCase())) {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Advertencia',
        text: `Ya existe el cargo`,
        icon: 'warning',
      });
      return;
    }else {
      this.cargoService.nuevoCargoAdmin(cargoAdmin).subscribe((resp: any) => {

        if (resp) {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Almacenado correctamente`,
            icon: 'success',
          });
          this.formAdminCargo.reset();
          this.llenarComboCargos();
          this.reload();
        }

      }, (err: any) => {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Advertencia',
          text: `Ocurrio un problema`,
          icon: 'warning',
        });
      });
    }
  }

  private iniciarFormularioCargoAdmin(): FormGroup {
    return this.fbdos.group({
      nombrecargo: ['', [Validators.required, Validators.minLength(4), Validators.pattern(isCaracter)]],
    });

  }

  esCampoValidoAdminCargo(campo: string) {
    const validarCampo = this.formAdminCargo.get(campo);
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }

  existeCargo(nombre: string): boolean {
    const normalizedNombre = nombre.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
    return this.cargos.some(cargo => {
      const normalizedCargo = cargo.nombrecargo.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
      return normalizedCargo.toLocaleLowerCase() === normalizedNombre.toLocaleLowerCase();
    });
  }

  mostrarRecargando() {//recarga los datos
    let currentUrl = this.router.url;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
    this.router.navigate([currentUrl]);
  }

  /**Para recargar */
  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      this.admin = []
      dtInstance.destroy()
      this.cargarDatosAdmin()
    })
  }
}
