import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { EntradaPaginacionAdmin, IAdministrador, ICargo } from '../interface/administrador';
import { catchError } from 'rxjs/operators';
import Swal from 'sweetalert2';
import { Usuario } from '../../usuario/models/usuario';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AdministradorService {
  url = environment.urlG



  constructor(
    private http: HttpClient) { }

    public mostrarCargos() {
      return this.http.get<IAdministrador[]>(`${this.url}/cargo/listaCargos`);

    }

    public nuevoCargoAdmin(cargo:ICargo):any{
      return this.http.post(`${this.url}/cargo/guardar`,cargo);
    }

    public mostrarAdminUser(): Observable<any> {
      return this.http.get<any>(`${this.url}/administrador/listaAdminResp`);
    }

    public mostrarAdmin(): Observable<any> {
      return this.http.get<any>(`${this.url}/administrador/listaAdmin`);
    }
    public nuevoAdmin(admin:IAdministrador):any{
      return this.http.post(`${this.url}/administrador/guardarAdmin`,admin);
    }

    public deleteAdmin(admin:IAdministrador){
        return this.http.delete(`${this.url}/administrador/${admin.id}`).pipe(
          catchError(e =>{
              Swal.fire('error', 'Algo ocurrio mal, intente nuevamente')
              return throwError(e);
          })
        );
      }
    public editarAdmin(admin:IAdministrador):any{
       return this.http.put(`${this.url}/administrador/editarAdmin`,admin);
     }

     buscar(filtro: string, page: number=0, size: number){
      return this.http.get<any>(`${this.url}/administrador/buscar/${filtro}` + '?' + `page=${page}&size=${size}`)
    }

    public UsuariosListaAdmin(): Observable<Usuario[]> {
      return this.http.get<Usuario[]>(`${this.url}/usuario`);
    }
}
